import React, { Component } from 'react';
import DatePicker from './pages/Datepicker';
import { isMobile, isBrowser } from "react-device-detect";
import './App.css';

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			car_availability: null,
			car_prices: null
		};
	}

	componentDidMount() {
		if (isMobile) {
			document.getElementsByTagName("html")[0].classList.add('is-mobile-device');
		}

		if (isBrowser) {
			document.getElementsByTagName("html")[0].classList.add('is-desktop-device');
		}

		fetch("https://run.mocky.io/v3/d1b5c91e-2637-4d86-b653-db32c1081724")
			.then(res => res.json())
			.then(
				(result) => {
					this.setState({
						car_availability: result.availability,
						car_prices: result.prices
					});
				},
				(error) => {
					console.log(error)
				}
			)
	}
	render() {
		return (
			<div className="App" >
				{this.state.car_availability 
					? <DatePicker
						car_availability={this.state.car_availability}
						car_prices={this.state.car_prices}
					/>: null
				}
			</div>
		);
	}
}

export default App;