import React, { Component } from 'react';
import 'react-dates/initialize';
import { DayPickerRangeController, DateRangePicker, isInclusivelyAfterDay } from 'react-dates';
import {
    isBrowser,
    isMobile
} from "react-device-detect";
import moment from 'moment';
import 'react-dates/lib/css/_datepicker.css';
import './style.css';

class DatepickerWrapp extends Component {
    constructor(props) {
        super(props);

        this.state = {
            height: 0,
            headerDay: 0,
            selectOnlyStart: false,
            hideDatePicker: false,
            calendarFocused: false
        };
    }

    renderDay = (day) => {
        const daySize = this.props.daySize;

        return (
            <div className="CalendarDay__item" style={{ height: Math.ceil(daySize)}}>
                {day.format('D')}
            </div>
        )
    }

    lookupDay = (day) => {
        const { car_prices } = this.props;
        let item = car_prices[day.format("YYYY-MM-DD")];

        if (item === undefined) {
            item = null;
        }
        return item;
    }

    renderDayDesktop = (day) => {
        return (
            <div className="CalendarDay__item">
                {day.format('D')}
            </div>
        )
    }

    renderDayDesktopWithPrice = (day) => {
        let item = this.lookupDay(day);

        return (
            <div className="CalendarDay__item">
                {day.format('D')}
                {item !== null &&
                    <span className='CalendarDay_tooltip'>{item === null ? item : "$" + item}</span>
                }
            </div>
        )
    }

    isDayBlocked = day => {
        const { car_availability } = this.props;
        let notValid = false;
        let item = car_availability[day.format("YYYY-MM-DD")];

        if (item === 'unavailable') {
            notValid = true;
        }
        return notValid;
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.startDate && !nextProps.endDate) {
            this.setState({selectOnlyStart: true})
        } else if (!nextProps.startDate && !nextProps.endDate) {
            this.setState({selectOnlyStart: false})
        } else if (nextProps.startDate && nextProps.endDate) {
            this.setState({selectOnlyStart: false})
        }
    }

    render() {
        const { startDate, endDate, focusedInput, daySize, onDatesChange, onFocusChange, headerHeight, bottomHeight, homeDatePicker, cardDatePicker } = this.props;
        let heightWrapp;

        if (headerHeight > 0 && bottomHeight > 0) {
            heightWrapp = window.innerHeight - headerHeight - bottomHeight - 15
        }

        return (
            <div className={`Datepicker-wrapp ${this.state.selectOnlyStart ? 'is-selecting-start' : null}`} style={isMobile ? { height: heightWrapp } : null}>
                {isMobile &&
                    <DayPickerRangeController
                        startDate={startDate}
                        endDate={endDate}
                        onDatesChange={onDatesChange}
                        focusedInput={focusedInput}
                        onFocusChange={onFocusChange}
                        orientation="verticalScrollable"
                        numberOfMonths={12}
                        renderDayContents={this.renderDay}
                        noNavButtons
                        noBorder
                        minimumNights={0}
                        daySize={daySize}
                        hideKeyboardShortcutsPanel={true}
                        isOutsideRange={day => !isInclusivelyAfterDay(day, moment())}
                    />
                }
                {isBrowser && homeDatePicker && !cardDatePicker
                    ? <DateRangePicker
                        startDate={startDate}
                        endDate={endDate}
                        startDateId='start'
                        endDateId='end'
                        onDatesChange={onDatesChange}
                        focusedInput={focusedInput}
                        onFocusChange={onFocusChange}
                        renderDayContents={this.renderDayDesktop}
                        minimumNights={0}
                        numberOfMonths={2}
                        noBorder
                        hideKeyboardShortcutsPanel={true}
                        isOutsideRange={day => !isInclusivelyAfterDay(day, moment())}
                    /> : null 
                }
                {isBrowser && cardDatePicker && !homeDatePicker
                    ? <DateRangePicker
                        startDate={startDate}
                        endDate={endDate}
                        startDateId='start'
                        endDateId='end'
                        onDatesChange={onDatesChange}
                        focusedInput={focusedInput}
                        onFocusChange={onFocusChange}
                        renderDayContents={this.renderDayDesktopWithPrice}
                        minimumNights={0}
                        numberOfMonths={2}
                        noBorder
                        hideKeyboardShortcutsPanel={true}
                        isOutsideRange={day => !isInclusivelyAfterDay(day, moment())}
                        isDayBlocked={this.isDayBlocked}
                    /> : null
                }
            </div>
        )
    }
}

export default DatepickerWrapp;